.ui.card {
  position: relative;
}

.ui.card>.content, .ui.cards>.card>.content {
  display: flex;
  flex-direction: column;
}

.ui.card>.content :last-child {
  margin-top: auto;
}

.ui.card>.content :nth-last-child(2) {
  margin-bottom: 0.75rem;
}

.ui.card:hover {
  box-shadow: 0 0 0 0.25rem;
  color: #2185d0;
  cursor: pointer;
}

.ui.card a {
  color: black;
}

.ui.card a::after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
}

.ui.card a:focus {
  text-decoration: underline;
}

.ui.card:focus-within {
  box-shadow: 0 0 0 0.25rem;
  color: #2185d0;
}

.ui.card:focus-within a:focus {
  text-decoration: none;
}

h1 {
  margin-bottom: 1rem;
}

.cards > ul {
  list-style: none;
}

.card + .card {
  margin-top: 1.5rem;
}

@supports (display: grid) {
  .cards > ul {
      display: grid;
      grid-template-columns: repeat(auto-fill, minmax(15rem, 1fr));
      grid-column-gap: 1.5rem;
      grid-row-gap:1.5rem;
  }

  .card + .card {
      margin-top: 0;
  }
}
